.pcicingSection {
  padding: 70px 0px;
}
@media screen and (min-width: 1024px) {
  .pcicingSection {
    padding: 150px 0px;
  }
}
.pricingWrapper {
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
  padding: 0px 15px;
}
.pricingTextBlock {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media screen and (min-width: 1024px) {
  .pricingTextBlock {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.pricingList {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
  max-width: 640px;
  margin: 0;
  padding: 0;
}
.pricingHeader {
  display: block;
  margin-bottom: 70px;
}
.pricingItem {
  color: var(--text-color-black);
  font-size: 22px;
  font-weight: 500;
  line-height: 31px;
  letter-spacing: 0.02em;
  text-align: left;
  margin-bottom: 35px;
}
.pricingItem:last-child {
  margin-bottom: 50px;
}
.feeButton {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  border-radius: 200px;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0em;
  padding: 25px 55px 25px 55px;
  border-radius: 50px;
  border: 1px;
  background-image: var(--gradient-text);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  border: 1px solid #4f86e3;
  transition: background-image 0.3s linear, color 0.3s linear;
}

.feeButton:hover,
.feeButton:focus {
  background: var(--gradient-text);
  color: var(--text-color-white);
}

.arrowSvg {
  stroke: #4f86e3;
  margin-left: 14px;
  transition: stroke 0.3s linear, fill 0.3s linear;
}
.feeButton:hover .arrowSvg {
  stroke: var(--text-color-white);
}

/* Modal */
.modalBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5px 40px;
}
.modalForm {
  display: block;
}
.formMainLabel {
  display: block;
  text-align: center;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.02em;
  color: var(--text-color-black);
}
.formSecondaryLabel {
  display: block;
  text-align: center;
  margin-bottom: 30px;
  font-size: 16px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0.02em;
}
.modalInputBlock {
  display: flex;
  margin-bottom: 15px;
  flex-direction: column;
  gap: 5px;
}
.formLabel {
  font-size: 16px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0.02em;
  text-align: left;
}
.formInput {
  min-width: 350px;
  height: 50px;
  padding: 0px 15px;
  border-radius: 12px;
  border: 1px solid #9e9d9d;
}
.formInputRequest {
  white-space: pre-wrap;
  font-family: 'Neue Haas Grotesk Display Pro';
  resize: none;
  font-size: 16px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0.02em;
  min-width: 350px;
  height: 70px;
  padding: 15px;
  border-radius: 12px;
  border: 1px solid #9e9d9d;
}
.formInput::placeholder {
  font-size: 16px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0.02em;
  padding: 0;
}
.formInputRequest::placeholder {
  font-family: 'Neue Haas Grotesk Display Pro';
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0.02em;
  text-align: left;

  font-size: 16px;
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  white-space: pre-wrap;
}
.formInput:focus {
  border-color: #9e9d9d;
}
.modalSupport {
  display: flex;
  flex-direction: row;
}
.modalCheckbox {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.modalRemember {
  display: block;
  color: var(--text-color-black);
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0.02em;
  text-align: left;

  margin-right: 130px;
}
.modalForgotPass {
  display: block;
  text-decoration: none;
  color: var(--text-color-black);
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0.02em;
}
.notClient {
  display: flex;
  justify-content: center;
  color: var(--text-color-black);
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0.02em;
  text-align: left;
}
.notClientSend {
  text-align: block;
  margin-left: 5px;
  color: var(--text-color-black);
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0.02em;
  text-align: left;
}
/* Button */

.loginButton {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  margin-top: 30px;
  margin-bottom: 30px;
  width: 100%;
  height: 74px;
  padding: 15px 0px 15px 0px;
  border-radius: 50px;
  font-family: 'Neue Haas Grotesk Display Pro';
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
  border: none;
  border-radius: 50px;
  background: var(--gradient-text);
  color: var(--text-color-white);
  position: relative;
  transition: background-color 0.3s linear;
}

.loginButton:hover,
.loginButton:focus {
  background: linear-gradient(103.88deg, #3b78df 7.98%, #5859de 102.57%);
}
